h1 {
    font-size: 50px;
    color: #f1faee;
    font-weight: bold;
}

.volunteer {
    color: #f1faee;
    font-size: 25px;
    font-weight: bold;
}

h2 {
    font-size: 40px;
    color: #f1faee;
}

.skill {
    text-decoration: underline 5px #9a1aef;
}

.list-group-item.list-group-item {
    background-color: #480872;
    color: #f1faee;
    font-size: 20px;
}

.body {
    background-color: #9a1aef;
    font-size: 25px;
    color: #f1faee;
}

.accordion button {
 background-color: #9a1aef;
}

.accordion button:not(.collapsed) {
    background-color: #9a1aef;
    color: black;
    box-shadow: none;
}

.accordion button:not(.collapsed)::after {
    background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16' fill='%23212529'%3e%3cpath fill-rule='evenodd' d='M1.646 4.646a.5.5 0 0 1 .708 0L8 10.293l5.646-5.647a.5.5 0 0 1 .708.708l-6 6a.5.5 0 0 1-.708 0l-6-6a.5.5 0 0 1 0-.708z'/%3e%3c/svg%3e");
}

.accordion button:focus {
    box-shadow: none;
    border-color: none;
}

.accordion h1 {
    color: #f1faee;
}

.btn.btn-outline-download {
    background-color: #9a1aef;
    color: #f1faee;
    font-size: 25px;
}

.btn.btn-outline-download:hover {
    background-color: #f1faee;
    color: #9a1aef;
    font-weight: bold;
}

.btn.btn-outline-download:focus {
    box-shadow: none;
}

.btn.btn-outline-five {
    background-color: #f1faee;
    color: #9a1aef;
    font-size: 25px;
    font-weight: bold;
    margin: 60px 0px 55px 0px;
}

.btn.btn-outline-five:hover {
    background-color: #9a1aef;
    color: #f1faee;
    transform: rotate(360deg);
    transition: 2s;
}